import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RpgComponent } from './rpg/rpg.component';
import { HelpRpgComponent } from './help-rpg/help-rpg.component';
import { IonicModule } from '@ionic/angular';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RpgFormComponent } from './rpg-form/rpg-form.component';
import { CriacoesModule } from './criacoes.module';
import { PaginacaoModule } from '../utils/paginacao/paginacao.module';
import { ListasModule } from '../utils/listas/listas.module';
import { ToastModule } from 'primeng/toast';
import { MessageService } from 'primeng/api';
import { HistoriaModule } from './historia.module';
import { LoadSpinnerModule } from '../utils/load-spinner/load-spinner.module';



@NgModule({
  declarations: [RpgComponent, HelpRpgComponent, RpgFormComponent],
  imports: [
    CommonModule,
    IonicModule,
    FormsModule,
    ReactiveFormsModule,
    CriacoesModule,
    PaginacaoModule,
    ListasModule,
    ToastModule,
    HistoriaModule,
    LoadSpinnerModule
  ],
  exports: [RpgComponent, HelpRpgComponent, RpgFormComponent],
  entryComponents: [RpgComponent, HelpRpgComponent, RpgFormComponent],
  providers: [MessageService]
})
export class RpgModule { }
